import { FC } from "react";
import { Card } from "@/components/ui/card";
import { ArrowRight, Check } from "lucide-react";
import { useTranslations } from "next-intl";
import { getProducts } from "@/utils/data";
import Link from "next/link";
import { useLocale } from "next-intl";


export const Products: FC = () => {
  const t = useTranslations('Products');
  const values = getProducts(t);
  const localeActive = useLocale();
  return (
    <section id="products" className="py-24 relative">
      <div className="absolute inset-0 bg-gradient-to-t from-card/30 to-transparent"></div>

      <div className="container mx-auto px-4 relative z-10">
        {/* Header */}
        <div className="text-center max-w-screen-xl mx-auto mb-16 space-y-8 animate-fade-in">
          <h2 className="text-4xl md:text-5xl font-bold text-foreground">
            {t('title')}
          </h2>
          <p className="text-xl text-muted-foreground">
            {t('p')}
          </p>
        </div>

        {/* Product Cards */}
        <div className="grid grid-cols-1 lg:grid-cols-3 xl:grid-cols-3 gap-8">
          {values.map((product, index) => {
            const Icon = product.icon;
            return (
              <Card
                key={index}
                className="p-8 bg-card/80 backdrop-blur-sm border-2 border-border rounded-2xl hover:border-primary/50 transition-all duration-300 group hover:-translate-y-2 animate-slide-up"
                style={{ animationDelay: `${index * 0.1}s` }}
              >
                {/* Header */}
                <div className="flex items-center gap-4 mb-6">
                  <div
                    className={`w-14 h-14 rounded-xl bg-${product.color}/10 flex items-center justify-center group-hover:scale-110 transition-transform`}
                  >
                    <Icon className={`w-7 h-7 text-${product.color}`} />
                  </div>
                  <div className="flex-1">
                    <h3 className="xl:text-2xl font-bold mb-2 text-foreground h-15">{product.title}</h3>
                  </div>
                </div>
                <p className="text-muted-foreground">{product.description}</p>

                {/* Features */}
                <ul className="space-y-3 my-6">
                  {product.features.map((feature, idx) => (
                    <li key={idx} className="flex items-center text-muted-foreground">
                      <Check className={`w-4 h-4 text-${product.color}`} />
                      {feature}
                    </li>
                  ))}
                </ul>
                {/* Animated Demo */}
                <div className="h-[400px] pb-6 overflow-hidden mb-5 border border-primary/20 rounded-2xl">
                  {product.demo}
                </div>

                <Link href={`/${localeActive}/products/${product.id}`}
                  className="w-full border border-primary rounded-xl hover:bg-primary/10 group-hover:border-primary/60 mt-auto flex justify-center items-center py-3 text-primary font-semibold transition-all"
                >
                  <ArrowRight className="ml-2 h-4 w-4 group-hover:translate-x-1 transition-transform" />
                  {t('button')}
                </Link>
              </Card>
            );
          })}
        </div>
      </div>
    </section>
  );
};
